@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@600&family=Raleway:wght@700&display=swap);
:root {
  --main-bg-color: #ffffff;
  --bg-delete-button-color: #eb5757;
  --state-selected-color: #2f80ed; /*Also 'add' button color*/
  --states-border-color: #bdbdbd;
  --checkbox-border-color: #828282;
  --main-text-color: #333333;
  --todo-text-color: #828282;
  --copyright-text-color: #a9a9a9;
  --shadow-add-button-color: rgba(127, 177, 243, 0.4);
  --cant-states: 3;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-size: 62.5%;
}

body {
  color: #333333;
  color: var(--main-text-color);
}

.main .states-list {
  margin-bottom: 18px;
}

.TODOS {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.TODOS .todo-button:hover {
  cursor: pointer;
}

.TODOS .todo-button--add {
  margin-left: 25px;
}

.TODOS .todo-button--delete {
  position: relative;
  left: 250px;
}

.search-items {
  margin-bottom: 30px;
}

.footer {
  width: 100%;
  height: 40px;
  position: absolute;
  left: 0;
  bottom: 0;
  text-align: center;
}

.footer__copyright {
  color: var(--copyright-text-color);
  font-family: "Montserrat", sans-serif;
  font-size: 1.4rem;
  font-weight: 500;
}

.footer__copyright--username {
  font-weight: 700;
  text-decoration: underline;
}

.Header {
  width: 100%;
  height: 100px;
  padding-top: 32px;
}

.Header__title {
  height: 42px;
  font-family: "Raleway", sans-serif;
  font-weight: 700;
  font-size: 3.6rem;
  text-align: center;
}

.states-list {
  display: flex;
  width: calc(217px * var(--cant-states));
  height: 53px;
  margin: 0 auto;
  position: relative;
  justify-content: space-evenly;
  align-items: center;
  font-family: "Montserrat", sans-serif;
  font-weight: 600;
  font-size: 1.4rem;
  list-style: none;
}

.states-list:after {
  content: "";
  width: 608px;
  height: 1px;
  position: absolute;
  bottom: 0;
  background-color: var(--states-border-color);
}

.states-list__state,
.states-list__state--selected {
  width: 89px;
  text-align: center;
}

.states-list__state:hover,
.states-list__state--selected:hover {
  cursor: pointer;
}

.states-list__state--selected {
  position: relative;
}

.states-list__state--selected:after {
  content: "";
  width: 89px;
  height: 4px;
  position: absolute;
  left: 0;
  bottom: -17px;
  border-radius: 4px 4px 0 0;
  background-color: var(--state-selected-color);
  z-index: 1;
}

.todo-button {
  font-family: "Montserrat", sans-serif;
  font-weight: 600;
  font-size: 1.4rem;
  color: var(--main-bg-color);
  border-color: transparent;
}

.todo-button--add {
  padding: 20px 40px;
  border-radius: 12px;
  background-color: var(--state-selected-color);
  box-shadow: 0 2px 6px rgba(127, 177, 243, 0.4);
}

.todo-button--delete {
  padding: 12px 26px;
  border-radius: 4px;
  background-color: var(--bg-delete-button-color);
}

.todo-button__trash {
  display: inline-block;
  width: 15px;
  height: 15px;
  position: relative;
  top: 2px;
  background-image: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABgAAAAYCAQAAABKfvVzAAAAPklEQVQ4y2NgGPzgv8L/+/9h4P5/BXxK8QDqaCDH/aSaT5YGYsRGNQx5DY+wpoxHuDV4/H+Mofzxf49BnucBABbzK/wrsYUAAAAASUVORK5CYII=);
  background-position: center;
  background-size: cover;
}

.todo-search {
  width: 476px;
  height: 56px;
  padding: 20px 0 20px 12px;
  border: 1px solid var(--states-border-color);
  border-radius: 12px;
}

.todo-list {
  width: 615px;
  list-style: none;
}

.todo-list__todo-item {
  position: relative;
  margin-bottom: 25px;
  font-family: "Montserrat", sans-serif;
  font-size: 1.8rem;
  font-weight: 500;
}

.todo-list__todo-item.completed {
  text-decoration: line-through;
}

.todo-list__todo-item input {
  margin-left: 5px;
  margin-right: 12px;
  -webkit-transform: scale(1.85);
          transform: scale(1.85);
}

.todo-item__trash {
  display: inline-block;
  width: 20px;
  height: 21px;
  position: absolute;
  right: 0;
  background-image: url(/todo-list/static/media/removeTodo.169daebf.svg);
}

.todo-item__trash:hover {
  cursor: pointer;
}

